#matrix-scene {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: #000000;
  overflow: hidden; }
  #matrix-scene .matrix-play-button,
  #matrix-scene .matrix-camera-button,
  #matrix-scene .matrix-copy-button {
    position: absolute;
    top: -50px;
    left: 0px;
    margin: 10px;
    border: 0px;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 40px;
    height: 40px;
    appearance: none;
    transition: 0.3s top;
    cursor: pointer; }
    #matrix-scene .matrix-play-button.show,
    #matrix-scene .matrix-camera-button.show,
    #matrix-scene .matrix-copy-button.show {
      top: 0px; }
  #matrix-scene .matrix-camera-button {
    left: 60px; }
  #matrix-scene .matrix-copy-button {
    left: 120px; }
    #matrix-scene .matrix-copy-button::after {
      position: absolute;
      top: 0%;
      left: 0%;
      transition: all 0.5s;
      opacity: 0;
      border-radius: 50%;
      background: #ffffff;
      width: 100%;
      height: 100%;
      content: ""; }
    #matrix-scene .matrix-copy-button:active::after {
      top: 30%;
      left: 30%;
      transition: all 0s;
      opacity: 1;
      width: 40%;
      height: 40%; }
  #matrix-scene .matrix-color,
  #matrix-scene .matrix-font-size {
    position: absolute;
    right: 0px;
    bottom: -50px;
    margin: 10px;
    transition: 0.3s bottom; }
    #matrix-scene .matrix-color.show,
    #matrix-scene .matrix-font-size.show {
      bottom: 0px; }
    #matrix-scene .matrix-color label,
    #matrix-scene .matrix-color input,
    #matrix-scene .matrix-font-size label,
    #matrix-scene .matrix-font-size input {
      text-align: right;
      color: #ffffff;
      font-family: Courier New, Courier, Andale Mono, monospace;
      font-size: 12px; }
    #matrix-scene .matrix-color label,
    #matrix-scene .matrix-font-size label {
      display: inline-block;
      margin-right: 5px;
      width: 50px;
      vertical-align: middle;
      text-shadow: 1px 1px 1px #000000;
      line-height: 12px; }
    #matrix-scene .matrix-color input,
    #matrix-scene .matrix-font-size input {
      border: 1px solid #ffffff;
      background: #000000;
      width: 40px;
      height: 20px; }
  #matrix-scene .matrix-color {
    right: 120px; }
    #matrix-scene .matrix-color .matrix-color-picker {
      vertical-align: middle; }
      #matrix-scene .matrix-color .matrix-color-picker.show {
        max-height: 230px;
        opacity: 1; }
  #matrix-scene .matrix-font-size {
    right: 20px; }
  #matrix-scene .matrix-video {
    display: none; }
  #matrix-scene .matrix-canvas,
  #matrix-scene .matrix-ascii {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%; }
  #matrix-scene .matrix-canvas {
    visibility: hidden; }
  #matrix-scene .matrix-ascii-text {
    cursor: default;
    line-height: 5px;
    color: #ffffff;
    font-size: 4px; }
    #matrix-scene .matrix-ascii-text::selection {
      background: transparent; }
  #matrix-scene .loading-overlay {
    position: fixed;
    width: 100%;
    height: 0px;
    overflow: hidden; }
    #matrix-scene .loading-overlay.show {
      height: 100%; }
    #matrix-scene .loading-overlay pre {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 16px;
      text-shadow: 0px 0px 2px #000000; }
